import {MaintainModel} from "../../model/maintain.js"
const maintainModel = new MaintainModel()
export default {
    name: "maintain",
    data(){
        return {
            list:[],
            choosed:[]
        }
    },
    methods:{
        touchmove(e){
            document.body.style.overflow='hidden';
            document.documentElement.style.overflow='hidden';
        },
        getData()
        {
            maintainModel.getData().then((res) => {
                console.log('10101010')
                let data = res.data
                this.list = data[0]['children']
            })
        },
        choose(index,key){
            let arr = this.list[index]['children'][key]
            if (arr['checked']) {
                arr['checked'] = false
                var index = this.choosed.indexOf(arr.id); 
                this.choosed.splice(index,1)
            } else {
                arr['checked'] = true
                this.choosed.push(arr.id)
            }
            this.list = JSON.parse(JSON.stringify(this.list))
            console.log(this.choosed)
        },
        toShop()
        {
            uni.navigateTo({
                url:`/pagesB/maintain_shop/maintain_shop?classify_ids=${JSON.stringify(this.choosed)}`
            })
        }
    },
    onLoad()
    {

    },
    onShow(){
        this.choosed = []
        this.getData()
    },
    watch:{
        list:{
            handler(newValue, oldValue) {
                console.log(newValue,oldValue)
            }
        },
        deep: true
    }
}
